import React, {useState} from 'react'
import SuperEditableSpan from './common/c4-SuperEditableSpan/SuperEditableSpan'
import SuperButton from '../h4/common/c2-SuperButton/SuperButton'
import {restoreState, saveState} from './localStorage/localStorage'
import {IoIosCheckmarkCircleOutline, MdRestore} from "react-icons/all";


function HW6() {
    const [value, setValue] = useState<string>('')

    const save = () => {
        saveState<string>('editable-span-value', value)
    }
    const restore = () => {
        setValue(restoreState('editable-span-value',value))
    }

    return (
        <div style={{backgroundColor:'chocolate'}}>
            <hr/>
            <h2>HomeTask 6</h2>


            {/*should work (должно работать)*/}
            <div>
                <SuperEditableSpan
                    value={value}
                    onChangeText={setValue}
                    spanProps={{children: value ? undefined : 'Click me, best proger...'}}
                />
            </div>
            <SuperButton onClick={save} > <IoIosCheckmarkCircleOutline size={'small'} color={'green'}/> </SuperButton>
            <SuperButton onClick={restore}><MdRestore size={'small' } color={'red'}/></SuperButton>

            <hr/>
            {/*для личного творчества, могу проверить*/}
            {/*<AlternativeSuperEditableSpan/>*/}
            <hr/>
        </div>
    )
}

export default HW6
